import { useEffect, useState } from "react";
import Rating from '@mui/material/Rating';
import getTipoVideo from '../../services/tipoVideo'
import cx from "classnames";
import SliderContext from "../context/SliderContext";
import ShowDetailsButton from "./ShowDetailsButton";
//Lazy loading
import { LazyLoadImage } from 'react-lazy-load-image-component';


import 'react-lazy-load-image-component/src/effects/blur.css'

//import Mark from './Mark'
import "./Item.scss";
//Components
import Button from "@material-ui/core/Button";

/**
 * Componente para mostrar el elemento en el slider
 * @param {Object} video 
 * @returns 
 */
const Item = ({ video,...props }) => {
  const [duracion, setDuracion] = useState([]);
  const [showDetails, setShowDetails] = useState(false);
  const [tipoVideo, setTipoVideo] = useState('')

  const toggleShowDetails = () => {
    setShowDetails(!showDetails);
  };

  const changeDuration = (duration) => {
    let tiempo = duration.split(':');
    let new_duration = "";
    if (tiempo) {
      if (tiempo[0] !== "00") {
        new_duration = tiempo[0] + "h";
      }
      if (tiempo[1] !== "00") {
        new_duration = " " + new_duration + " " + tiempo[1] + "min";
      }
      /* if (tiempo[2] !=="00") {
        new_duration = " "+new_duration+" "+tiempo[2]+"s";
      } */
    }
    return new_duration.trim(); // trim() para eliminar cualquier espacio en blanco adicional
  };
  const handleTipoVideo =  async () =>{
    let idTipoVideo = video.tipe_of_video
    const response = await getTipoVideo(idTipoVideo)
    setTipoVideo(response)

  }
  
  useEffect(() => {
    setDuracion(changeDuration(video.duration));
    handleTipoVideo()
    
  }, [duracion, video.duration]);
  return (
    <SliderContext.Consumer>
      {({ onSelectSlide, currentSlide, elementRef, history }) => {
        const isActive = currentSlide && currentSlide.id === video.id;
        
        return (
          <div
            ref={elementRef}
            className={cx("item", {
              "item--open": isActive,
            })}
          >
            <div key={video.id} className="card   cursorPointer card-video">
            <small className="d-flex justify-content-end px-3 pt-1 ">{tipoVideo.tipe_video == "videos" ? "Video": 
                                                                      tipoVideo.tipe_video == "casos" ? "Caso" : 
                                                                      tipoVideo.tipe_video == "series" ? "Serie" : ""  }
            </small>
              
              <Button onClick={() => props.verVideo(video.id) }>
                <LazyLoadImage effect="blur" src={`https://sccotview.com${video.featured_image}`} className="card-img-top cursorPointer imagen-container" alt="..." />
              </Button>
              {isActive}
              
              <div className="card-body p-0 px-2 d-flex flex-column justify-content-between" key={video.id}>
                <p className="truncate-text" onClick={(event) => {event.stopPropagation(); onSelectSlide(video);}}>
                  {video.title_espanol}
                </p>
                <div className="d-flex justify-content-between align-items-end" onClick={(event) => event.stopPropagation()}>
                  <div>
                    <p>{duracion}</p>
                  </div>
                  <div className="bg-secondary rounded-2 mb-2 pt-1" onClick={(event) => event.stopPropagation()}>
                    <Rating name="read-only" value={parseFloat(video.score)} precision={0.5} readOnly />
                  </div>
                </div>
              </div>
              <ShowDetailsButton onClick={() => onSelectSlide(video)}></ShowDetailsButton>
            </div>

          
          </div>
        );
      }}
    </SliderContext.Consumer>
  );
};

export default Item;
